import React from 'react';
import Form from './Form';
import List from './List';
import todo from './todo.json';

class App extends React.Component {

    constructor(props){
        super(props)
        this.state =  { defaultList: todo, checked: false }
    }
    
    onSubmit = (task, status, priority) => {
        if(this.state.checked) {
            console.log("これ");

            this.state.defaultList.unshift({task, status, priority});
            this.setState(todo);
            console.log("Todo", todo);
        } else {
            this.state.defaultList.unshift({task, status});
            this.setState(todo);            
        }
    }

    deleteTask = (i) => {
        this.state.defaultList.splice(i, 1);
        this.setState({defaultList: todo})
    }

    handleCheckBox = () => {
        this.setState({ checked: !this.state.checked });
        console.log("App.js", this.state.checked);       
    }    

    render() {
        return (
            <div className="ui container" style={{marginTop: '20px'}}>
                <Form 
                    onSubmit = {this.onSubmit}
                    checkCheckBox = {this.handleCheckBox}
                />
                <List 
                    lists = {this.state.defaultList}
                    deleteTask = {this.deleteTask}
                    checked={this.state.checked}
                />
            </div>
        );
    }
}

export default App;