import React from 'react';
import './Form.scss';

class Form extends React.Component {

    constructor(props){
        super(props)
        this.state =  { checkBoxChecked: false }
    }

    onFormSubmit = (event) => {
        event.preventDefault();

        if(this.refs.userInput.value === "") {
            this.refs.warning.innerHTML = "Please enter a task";
        } else {
            this.refs.warning.innerHTML = "";
            this.props.onSubmit(this.refs.userInput.value, "", "Important");
            this.refs.userInput.value = "";
        }
        
    }

    handleCheckClick = (event) => {
        this.setState({checkBoxChecked: !this.state.checkBoxChecked});
        this.props.checkCheckBox();
    }

    

    render() {
        return (
            <div className="ui segment">
                <form onSubmit={this.onFormSubmit} className="additem ui form">
                    <div className="field">
                        <label>Task</label>
                        <input ref="userInput" />
                        <p ref="warning" className="warning"></p>
                    </div>
                    <div className="ui checkbox block">
                        <input
                            ref = "checkPriority"
                            type="checkbox"                      
                            onChange={this.handleCheckClick} 
                            checked = {this.state.checkBoxChecked}
                        />
                        <label>Important</label>
                    </div>
                        <button className="ui button" type="submit">ADD</button>
                </form>
            </div>
        );
    }
}

export default Form;