import React from 'react';
import Item from './Item';
import './List.scss';

const List = ({deleteTask, lists}) => {
    const renderedList = lists.map((list, i) => {

        return (
            <Item 
                key={i}
                task = {list.task}
                priority = {list.priority}
                deleteItem = {deleteTask}
                // checkPriority = {checked}
            />
        );
    });


    return <div>{renderedList}</div>
}

export default List;